import { Component } from 'react'
import ImgBandeauDesktop from '../../assets/bandeaudesktop.png'
import ImgBandeauMobile from '../../assets/bandeauMobile.png'
import './main-bandeau.scss'

/**
 * Send Dom bandeau
 * @return { Dom }
 */
class Bandeau extends Component {
    render() {
        return (
            <div className="bandeau-container">
                <picture className="bandeau-content">
                    <source srcSet={ImgBandeauDesktop} media="(min-width: 768px)" />
                    <img className="bandeau-content__img" src={ImgBandeauMobile} alt=" Reprentation d'une falaise qui surplombe la mer avec la vue sur des montagnes en fond" />
                </picture>
            </div>
        )
    }
}

export default Bandeau;
