import { Link } from 'react-router-dom'
import { Component } from 'react'
import './main-header.scss'
import logoKasa from '../../assets/logoKasa.png'
/* function Header() {
  return (
    <nav>
      <Link to="/"> Accueil </Link>
      <Link to="/404"> error404 </Link>
    </nav>
  )
} */

class Header extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <div className="header-container">
        <div className="header-content">
        <img className="header-content__logo" src={logoKasa}  alt="logo de kasa"  />
          <nav className="header-content__nav">
            <Link className="header-content__nav-link" to="/"> Accueil </Link>
            <Link className="header-content__nav-link" to="/About"> A Propos </Link>
          </nav>
        </div>
      </div>
    )
  }
}

export default Header